<template>
  <d2-container>
    <template slot="header">
      <instence-select :isupf="false" @changeInstence="changeInstance"></instence-select>
    </template>

    <el-card class="box-card">
      <el-form label-position="left" class="demo-table-expand" size="mini">
        <el-form-item label="N6接口接收GTP包数"
          ><span>{{ serverData.upf_n6_inc_pkt }}</span></el-form-item
        >
        <el-form-item label="N6接口发送GTP包数"
          ><span>{{ serverData.upf_n6_og_pkt }}</span></el-form-item
        >
        <el-form-item label="N6接口接收GTP包字节数"
          ><span>{{ serverData.upf_n6_inc_oct }}</span></el-form-item
        >
        <el-form-item label="N6接口发送GTP包字节数"
          ><span>{{ serverData.upf_n6_og_oct }}</span></el-form-item
        >
        <el-form-item label="N6接口接收错误GTP包数"
          ><span>{{ serverData.upf_n6_disc_pkt }}</span></el-form-item
        >
      </el-form>
    </el-card>

    <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
      <el-tab-pane
        v-for="(item, index) in tabPaneList"
        :key="index"
        :label="item.tabLabel"
        :name="item.tabLabel"
      >
        <el-table :data="serverData.n6IncPktDnnList" style="width: 100%">
          <!-- <el-table-column prop="id" label="ID" width="180">
          </el-table-column> -->
          <el-table-column prop="dnn_name" label="DN_Name"> </el-table-column>
          <el-table-column prop="n6_inc_pkt" label="N6接口接收GTP包数">
          </el-table-column>
          <el-table-column prop="n6_og_pkt" label="N6接口发送GTP包数">
          </el-table-column>
          <el-table-column prop="n6_inc_oct" label="N6接口接收GTP包字节数">
          </el-table-column>
          <el-table-column prop="n6_og_oct" label="N6接口发送GTP包字节数">
          </el-table-column>
          <el-table-column prop="n6_disc_pkt" label="N6接口接收错误GTP包数">
          </el-table-column>
        </el-table>
      </el-tab-pane>
    </el-tabs>
  </d2-container>
</template>

<script>
import {
  fetchGetPerformN6,
} from "@/api/demo/index";
import instenceSelect from '../compoents/instenceSelect.vue';
export default {
  components: { instenceSelect },
  name: "performN6",
  data() {
    return {
      activeName: "分DN统计",
      instanceValue: "", //当前选中的实例
      isFirst: true,
      loading: false,
      serverData: {},
      tabPaneList: [
        { tabLabel: "分DN统计" },
        // { tabLabel:'分DNN的N6接口接收GTP包数', dataKey: 'n6IncPktDnnList', columKey: 'n6_inc_pkt' },
        // { tabLabel:'分DNN的N6接口发送GTP包数', dataKey: 'n6OgPktDnnList', columKey: 'n6_og_pkt' },
        // { tabLabel:'分DNN的N6接口接收GTP包字节数', dataKey: 'n6IncOctDnnList', columKey: 'n6_inc_oct' },
        // { tabLabel:'分DNN的N6接口发送GTP包字节数', dataKey: 'n6OgOctDnnList', columKey: 'n6_og_oct' },
        // { tabLabel:'分DNN的N6接口接收错误GTP包数', dataKey: 'n6DiscPktDnnList', columKey: 'n6_disc_pkt' }
      ],
    };
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event);
    },
    // 选择实例
    changeInstance(val) {
      // 图表置空
      this.serverData = {};
      this.instanceValue = val

      // 获取告警数据
      this.getData();
    },
    // 获取数据
    getData() {
      this.loading = true;
      fetchGetPerformN6({
        request_type: "n6 perform",
        instance_id: this.instanceValue,
        sequence_no: 3355,
      })
        .then((response) => {
          this.loading = false;
          console.log(response);
          if ((response != null) & (response != [])) {
            this.serverData = response;
            // 拼接组装表格数据
            if (this.serverData.n6IncPktDnnList) {
              this.serverData.n6IncPktDnnList.forEach((element, index) => {
                element.n6_og_pkt = this.serverData.n6OgPktDnnList[
                  index
                ].n6_og_pkt;
                element.n6_inc_oct = this.serverData.n6IncOctDnnList[
                  index
                ].n6_inc_oct;
                element.n6_og_oct = this.serverData.n6OgOctDnnList[
                  index
                ].n6_og_oct;
                element.n6_disc_pkt = this.serverData.n6DiscPktDnnList[
                  index
                ].n6_disc_pkt;
              });
            }
          }
        })
        .catch((e) => {
          this.loading = false;
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
  },
};
</script>

