<template>
<div>
  <el-select
    v-if="!isupf"
    v-model="serviceName"
    placeholder="请选择服务"
    size="small"
    @change="changeService"
    filterable
    clearable>
    <el-option
      v-for="item in serviceOptions"
      :key="item"
      :label="item"
      :value="item">
    </el-option>
  </el-select>

  <el-select
    v-model="instanceValue"
    placeholder="请选择您的实例"
    size="small"
    @change="changeInstance"
    filterable
    clearable
    style="width: 200px">
    <el-option
      v-for="(item, index) in instanceOptions"
      :key="index"
      :label="item.instanceId.split('#')[0]"
      :value="item.instanceId">
    </el-option>
  </el-select>
</div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "instenceSelect",
  props: {
    isupf: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      serviceName: null,
      serviceOptions: [],
      instanceOptions: [], //upf下面的实例
      instanceValue: null, //当前选中的实例
    }
  },
  computed: {
   ...mapState("d2admin", {
      serviceList: (state) => state.instence.serviceList,
      instenceList: (state) => state.instence.instenceList
    })
  },
  watch: {
    serviceList () {
      this.serviceOptions = this.$store.state.d2admin.instence.serviceList
      this.serviceName = this.serviceOptions[0]
     },
    instenceList () {
      this.instanceOptions = this.$store.state.d2admin.instence.instenceList
      this.instanceValue = this.instanceOptions[0].instanceId
      this.$emit('changeInstence', this.instanceValue)
     }
  },
  mounted() {
    if(this.isupf) {
      if (this.$store.state.d2admin.instence.serviceName && this.$store.state.d2admin.instence.serviceName === 'upf') {
        if(this.$store.state.d2admin.instence.instenceList) {
          this.instanceOptions = this.$store.state.d2admin.instence.instenceList
        }
        if(this.$store.state.d2admin.instence.instenceName) {
          this.instanceValue = this.$store.state.d2admin.instence.instenceName.instanceId
          this.$emit('changeInstence', this.instanceValue)
        }
      } else {
        this.serviceName = 'upf'
        this.$store.commit('d2admin/instence/setServiceName', 'upf')
        this.$store.dispatch("d2admin/instence/commitInstenceList")
      }
    } else {
      if(this.$store.state.d2admin.instence.serviceList.length < 1) {
        this.$store.dispatch("d2admin/instence/commitServiceList")
      } else {
        this.serviceOptions = this.$store.state.d2admin.instence.serviceList
        if(this.$store.state.d2admin.instence.serviceName) {
          this.serviceName = this.$store.state.d2admin.instence.serviceName
        }
        if(this.$store.state.d2admin.instence.instenceList) {
          this.instanceOptions = this.$store.state.d2admin.instence.instenceList
        }
        if(this.$store.state.d2admin.instence.instenceName) {
          this.instanceValue = this.$store.state.d2admin.instence.instenceName.instanceId
          this.$emit('changeInstence', this.instanceValue)
        }
      }
    }
    
  },
  methods: {
    // 选择服务
    changeService(val) {
      if (!val) {
        return;
      }
      this.$store.dispatch("d2admin/instence/commitInstenceList", val)
    },
    // 选择实例
    changeInstance(val) {
      if (val) {
        this.$emit('changeInstence', val)
        console.log('哈哈');
        console.log(val);
        let instenceItem = this.instanceOptions.filter(item => item.instanceId === val)
        if (instenceItem && instenceItem.length > 0) {
          this.$store.commit('d2admin/instence/setInstenceName', instenceItem[0])
        }
        console.log(instenceItem);
      }
    }
  }
};
</script>
